import React, { useState, ReactElement, useRef, RefObject, useEffect, useCallback, useMemo, createRef } from 'react';
import url from 'url';
import { FORM, server, POST } from '../utils/requests';
import { AnyDictionary, GameLinks, Player, TournamentSettings } from '../interfaces';

import styles from '../css/components.module.css';
import pairingStyles from '../css/pairings.module.css';
import { AiOutlineCheck, AiOutlineClose, AiOutlineMinus } from 'react-icons/ai';
import * as icons from '../assets/icons';
import * as regexes from '../utils/regexes';
import EditableField from './EditableField';
import { FaUndo } from 'react-icons/fa';

interface SinglePairingProps {
    result: [number | null, number | null]
    gameLinks: GameLinks
	tournamentID: string
	inputClassName: any
    hidden: AnyDictionary
    expanded: boolean
    expand: (set: boolean) => void
	validate: (form: HTMLFormElement) => boolean | string | void
	handleSubmit: (e: React.FormEvent<HTMLFormElement>) => Promise<React.FormEvent<HTMLFormElement>>
}

export default function SinglePairing(props: SinglePairingProps): ReactElement {

	const [valid, setValid] = useState(undefined as boolean | undefined);
	const [hover, setHover] = useState('');
	const [hasContent, setHasContent] = useState(false);

	const handleChange = (e: React.FormEvent<HTMLInputElement>) => {
		let input = e.target as HTMLInputElement;
		let form = input.parentElement as HTMLFormElement;
		let valid = props.validate(form);
		if (typeof valid === 'string') setHover(valid);
		else setHover('');
		if (typeof valid === 'boolean') setValid(valid);
		else if (typeof valid === 'string') setValid(false);
		else setValid(undefined);
		for (let child of Array.from(form.children)) {
			if (child.constructor.name !== 'HTMLInputElement') continue;
			if (child.getAttribute('type') === 'hidden') continue;
			if ((child as HTMLInputElement).value.toString()) {
				setHasContent(true);
				return;
			}
		}
		setHasContent(false);
	};

	const clear = (e: React.FormEvent<HTMLButtonElement>) => {
		e.preventDefault();
		let input = e.target as HTMLButtonElement;
		let div = input.parentElement as HTMLDivElement;
		let form = div.parentElement as HTMLFormElement;
		form.reset();
		setHover('');
		setValid(undefined);
	};
	
	const hasLink = useMemo(() => Object.values(props.gameLinks).some(v => v), [props.gameLinks]);

	if (props.result[0] !== null || props.result[1] !== null) return (
		<div className={styles.resultsBridge}>
			<div className={[styles.setResult, hasLink ? styles.linked : ''].join(' ')}>
				{props.result[0] ?? '\u200b'}
			</div>
			<div className={styles.neutral} onClick={() => props.expand(!props.expanded)} style={{
				transform: props.expanded ? 'rotate(450deg)' : 'rotate(0deg)'
			}}>
				<AiOutlineMinus />
			</div>
			<div className={[styles.setResult, hasLink ? styles.linked : ''].join(' ')}>
				{props.result[1] ?? '\u200b'}
			</div>
		</div>
	);
    
	return <form
		className={styles.resultsBridge}
		target='_self'
		onSubmit={(e) => {
			e.persist();
			if (!props.validate(e.target as HTMLFormElement)) return e.preventDefault();
			FORM(e).then(() => props.handleSubmit(e));
		}}
		method='POST'
		action={url.resolve(server, ['tournament', props.tournamentID, 'addResult'].join('/'))}
	>
		<input
			type='number'
			min={0}
			step={0.5}
			name='resultWhite' 
			required
			className={[props.inputClassName, hasLink ? styles.linked : '', hasContent || hasLink ? styles.alwaysVisible : '', hasLink ? styles.hasLink : ''].join(' ')}
			onChange={handleChange}
		/>
		<div className={styles.centre}>
			{hover ? <span className='tooltip'>{hover}</span> : null}
			{valid ?
				<button tabIndex={-1} className={styles.resolve}>
					<AiOutlineCheck />
				</button> :
				valid === false ?
					<button tabIndex={-1} className={styles.reject} onClick={clear}>
						<AiOutlineClose />
					</button> :
					<div className={styles.neutral} onClick={() => props.expand(!props.expanded)} style={{
						transform: props.expanded ? 'rotate(450deg)' : 'rotate(0deg)'
					}}>
						<AiOutlineMinus />
					</div>
			}
		</div>
		<input
			type='number'
			min={0}
			step={0.5}
			name='resultBlack'
			required
			className={[props.inputClassName, hasLink ? styles.linked : '', hasContent || hasLink ? styles.alwaysVisible : '', hasLink ? styles.hasLink : ''].join(' ')}
			onChange={handleChange}
		/>
		{Object.entries(props.hidden).map(([name, value], i) => {
			return <input key={['hiddenInput', i].join('.')} type='hidden' name={name} value={value} required />;
		})}		
	</form>;
}

interface DetailsProps {
    tournamentID: string
    id: string
    className: string
    expanded: boolean
    gameLinks: GameLinks
	individual: boolean
	round: number
    board: number
    update: () => Promise<void>
	triggerConfirm: (message: string, resolve: () => void, reject: () => void) => void
	updatePlayerDict: () => void
}

export function PairingDetails(props: DetailsProps): ReactElement {

	const [height, setHeight] = useState('0px');

	const details = useRef(null) as RefObject<HTMLDivElement>;
	const form = useRef(null) as RefObject<HTMLFormElement>;

	useEffect(() => {
		if (!details.current) return;
		if (!props.expanded) return;
		setHeight(details.current.getBoundingClientRect().height + 'px');
		return () => setHeight('0px');
	}, [details, setHeight, props.expanded]);

	const lichess = props.individual ? props.gameLinks.lichessLink : props.gameLinks.lichessTeamLink;
	const chessCom = props.individual ? props.gameLinks.chessComLink : props.gameLinks.chessComTeamLink;

	const handleSubmit = useCallback((key: 'lichess' | 'chessCom', value: string): Promise<void> => {
		let v = value;
		if (!props.individual) {
			let matches = value.match(regexes.chessComTeam);
			if (!matches) {
				console.error('Invalid syntax on tournament: ' + value);
				return Promise.resolve();
			}
			v = 'https://chess.com/club/matches/live/' + matches[1];
		}
		return POST({
			url: url.resolve(server, ['tournament', props.tournamentID, 'updateResultLink'].join('/')),
			data: {
				id: props.id,
				[key]: v,
				type: props.individual ? 'individual' : 'team',
				round: props.round
			}
		});
	}, [props.id, props.individual, props.tournamentID]);

	const handleClear = useCallback((key: 'lichess' | 'chessCom'): Promise<void> => {
		let [idW, idB] = props.id.split('.');
		return POST({
			url: url.resolve(server, ['tournament', props.tournamentID, 'clearResultLink'].join('/')),
			data: {
				idW,
				idB,
				site: key,
				type: props.individual ? 'individual' : 'team',
				round: props.round
			}
		});
	}, [props.id, props.individual, props.tournamentID]);

	const undoResult = useCallback((): Promise<void> => {
		let [idW, idB] = props.id.split('.');
		return POST({
			url: url.resolve(server, ['tournament', props.tournamentID, 'undoResult'].join('/')),
			data: {
				idW,
				idB,
				round: props.round
			}
		})
			.then(props.updatePlayerDict);
	}, [props.id, props.individual, props.tournamentID]);

	return (
		<div className={[props.className, styles.details].join(' ')} style={{
			height
		}}>
			<div ref={details} className={[styles.detailsContent].join(' ')}>
				<div className={styles.row}>
					Board: {props.board}{'\n'}
				</div>
				<button className={[styles.button, styles.infinity, styles.undoPairing].join(' ')} onClick={() => props.triggerConfirm('Really undo result? This action cannot be undone', undoResult, () => {})}>
					<FaUndo />
					Undo result
				</button>
				<div>
					<div className={styles.row}>
						For online games, enter {props.individual ? '' : 'team match '}game links below:
					</div>
					<div className={styles.row}>
						<EditableField
							content={chessCom}
							url={url.resolve(server, ['tournament', props.tournamentID, 'updateResultLink'].join('/'))}
							onSubmitSuccess={(e) => {
								props.update();
								return e;
							}}
							handleSubmit={(k: string, v: string) => handleSubmit('chessCom', v)}
							name={'link'}
							additionalInputProps={{}}
							additionalStyles={{
								input: styles.inputURL,
								editableField: styles.editableField
							}}
							formRef={form}
							useHandleSubmit
							handleClear={() => handleClear('chessCom')}
							show={{
								clear: true
							}}
						>
							<button className={[styles.submit, styles.linkSubmit].join(' ')}>
								<img src={icons.chessCom} alt='chess.com' />
							</button>
						</EditableField>
					</div>
					<div className={styles.row}>
						<EditableField
							content={lichess}
							url={url.resolve(server, ['tournament', props.tournamentID, 'updateResultLink'].join('/'))}
							onSubmitSuccess={(e) => {
								props.update();
								return e;
							}}
							handleSubmit={(k: string, v: string) => handleSubmit('lichess', v)}
							name={'link'}
							additionalInputProps={{}}
							additionalStyles={{
								input: styles.inputURL,
								editableField: styles.editableField
							}}
							formRef={form}
							useHandleSubmit
							handleClear={() => handleClear('lichess')}
							show={{
								clear: true
							}}
						>
							<button className={[styles.increment, styles.linkSubmit].join(' ')}>
								<img src={icons.lichess} alt='lichess' />
							</button>
						</EditableField>
					</div>
				</div>
			</div>
		</div>
	);
}

interface ManualPairingProps {
	tournamentID: string
	round: number
	handleSubmit: (e: React.FormEvent<HTMLFormElement>) => Promise<React.FormEvent<HTMLFormElement>>
	settings: TournamentSettings
	playerDict: Map<string, Player>
	close: () => void
}

export function ManualPairingForm(props: ManualPairingProps): ReactElement {

	const [W, setW] = useState(null as Player);
	const [B, setB] = useState(null as Player);

	const [valid, setValid] = useState(false);
	const [hover, setHover] = useState('');

	const getDisplayName = useCallback((id: string): string => {
		if (!id) return '\u200b';
		if (id === 'bye') return 'BYE';
		let opp = props.playerDict.get(id);
		if (!opp) return id;
		let displayName: string;
		if (props.settings.competitors === 'individual') displayName = [opp.firstName, opp.lastName].join(' ');
		else displayName = opp.firstName;
		return displayName;
	}, [props.playerDict, props.settings.competitors === 'individual']);

	useEffect(() => {
		setValid(false);
		if (!W || !B) return;
		if (W.id === 'bye' && B.id === 'bye') return setHover('Cannot pair BYE with BYE');
		if (W.histories && W.histories[props.round] && !W.histories[props.round].match) return setHover(getDisplayName(W.id) + ' is already paired');
		if (B.histories && B.histories[props.round] && !B.histories[props.round].match) return setHover(getDisplayName(B.id) + ' is already paired');
		setHover('');
		setValid(true);
	}, [W, B]);

	const handleInput = useCallback((e: React.KeyboardEvent<HTMLInputElement>) => {
		let input = e.target as HTMLInputElement;
		if (!(e.nativeEvent as any as InputEvent).data) {
			if (input.name === 'idW') setW(null);
			else setB(null);
			return;
		};
		let text = input.value;
		if (!text.trim()) return;
		let length = text.length;
		let choices = [{ id: 'bye'} as any as Player, ...Array.from(props.playerDict.values())].filter(p => getDisplayName(p.id).toLowerCase().startsWith(text.toLowerCase()));
		let firstChoice = choices[0];
		if (!firstChoice) return;
		let autofill = getDisplayName(firstChoice.id);
		input.value = autofill;
		input.select();
		input.setSelectionRange(length, autofill.length, 'forward');
		if (input.name === 'idW') setW(firstChoice);
		else setB(firstChoice);
	}, [props.playerDict]);

	const handleKeyDown = useCallback((e: React.KeyboardEvent<HTMLFormElement>) => {
		switch (e.keyCode) {
		case 27: {
			return props.close();
		}
		case 13: {
			(e.target as HTMLFormElement).submit();
		}
		}
	}, [props.close]);

	return (
		<form
			target='_self'
			onKeyDown={handleKeyDown}
			onSubmit={(e) => {
				e.preventDefault();
				POST({
					url: url.resolve(server, ['tournament', props.tournamentID, 'manualPairing'].join('/')),
					data: {
						idW: W.id,
						idB: B.id,
						round: props.round
					}
				}).then(() => props.handleSubmit(e));
			}}
			method='POST'
			action={url.resolve(server, ['tournament', props.tournamentID, 'manualPairing'].join('/'))}
		>
			<div className={pairingStyles.manualResultsBridge}>
				<input
					onInput={handleInput}
					autoComplete='off'
					placeholder='White'
					className={['playerInfo', pairingStyles.playerInfo].join(' ')}
					name='idW'
					style={{ gridArea: 'white'}}
				/>
				<div className={pairingStyles.scoreSpacer} style={{ gridArea: 'wR' }} />
				<div className={pairingStyles.centre} style={{ gridArea: 'dash' }}>
					{hover ? <span className='tooltip'>{hover}</span> : null}
					{valid ?
						<button tabIndex={-1} className={styles.resolve}>
							<AiOutlineCheck />
						</button> :
						<button tabIndex={-1} className={styles.reject} onClick={props.close}>
							<AiOutlineClose />
						</button>
					}
				</div>
				<div className={pairingStyles.scoreSpacer} style={{ gridArea: 'bR' }} />
				<input
					onInput={handleInput}
					autoComplete='off'
					placeholder='Black'
					className={['playerInfo', pairingStyles.playerInfo].join(' ')}
					name='idB'
					style={{ gridArea: 'black' }}
				/>
			</div>
		</form>
	);
}
